import {
  FETCH_PRODUTOS,
  CUSTOMER_LOGIN,
  CUSTOMER_LOGOUT,
  CUSTOMER_REGISTER,
  CUSTOMER_CART_ADD,
  CUSTOMER_CART_REM
} from "./actionTypes";

export function fetchProdutosAction(products) {
  return { type: FETCH_PRODUTOS, products };
}

export function customerLoginAction(dados) {
  return { type: CUSTOMER_LOGIN, dados };
}

export function customerLogoutAction() {
  return { type: CUSTOMER_LOGOUT };
}

export function customerRegisterAction(dados) {
  return { type: CUSTOMER_REGISTER, dados };
}

export function addItemAction(item) {
  return { type: CUSTOMER_CART_ADD, item };
}

export function remItemAction(item) {
  return { type: CUSTOMER_CART_REM, item };
}
