import {
  CUSTOMER_LOGIN,
  CUSTOMER_LOGOUT,
  CUSTOMER_REGISTER,
  CUSTOMER_CART_ADD,
  CUSTOMER_CART_REM
} from "../actions/actionTypes";

const INITIAL_STATE = {
  customer: false,
  cart: [],
  total: 0
};

export default function customerReducer(state = INITIAL_STATE, action) {
  switch (action.type) {
    case CUSTOMER_LOGIN:
      return {
        ...state,
        customer: action.data
      };
    case CUSTOMER_LOGOUT:
      return {
        ...state,
        customer: false,
        cart: [],
        total: 0
      };
    case CUSTOMER_REGISTER:
      return {
        ...state,
        customer: false,
        cart: [],
        total: 0
      };
    case CUSTOMER_CART_ADD:
      return {
        ...state,
        cart: state.cart.push(action.item),
        total: state.total + parseFloat(action.item.price)
      };
    case CUSTOMER_CART_REM:
      return {
        ...state,
        cart: state.cart.pop(),
        total: state.total - action.item.price
      };
    default:
      return state;
  }
}
